/* eslint-disable react/no-array-index-key */
/* eslint-disable no-unused-expressions */
/* eslint-disable radix */
/* eslint-disable no-nested-ternary */
import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Grid from "@material-ui/core/Grid";
import Typography from "../Typography";
import Button from "../Button";
import LoadingContent from "../LoadingContent";
import Box from "../Box";
import Logo from "../Logo";
import { styles } from "./styles";

const CardResumedFooter = ({
    classes,
    onClick,
    pax,
    onBuy,
    isLoading,
    type,
    isMobile,
    src,
    labelButton,
    mathSignDiff,
    totalPricePerAdult,
    totalPrice,
    isLogo
}) => {
    const renderLoadingCard = () => {
        return (
            <Grid container className={classes.price}>
                <Grid item xs={2}>
                    <LoadingContent />
                </Grid>
                <Grid item xs={2}>
                    <LoadingContent />
                </Grid>
            </Grid>
        );
    };

    const renderCardPrice = () => {
        return (
            <Grid container className={classes.price}>
                {isMobile ? (
                    <>
                        <Grid item xs={6}>
                            <Typography type="body2semibold">{mathSignDiff}R$</Typography>
                            <Typography type="headline3">{totalPricePerAdult}</Typography>
                            <Typography type="body2"> / Adulto</Typography>
                            <span
                                style={{
                                    display: "flex",
                                    alignItems: "baseline"
                                }}
                            >
                                <Typography className={classes.priceLabel} type="captionregular">
                                    {type !== "packages" ? `${pax} passageiros` : `${pax} viajantes`}
                                </Typography>
                                <Typography
                                    type="captionsemibold"
                                    className={
                                        type !== "packages" ? classes.priceTotalAir : classes.priceTotalPackage
                                    }
                                >
                                    {totalPrice}
                                </Typography>
                            </span>
                        </Grid>
                        <Grid item xs={4} align="flex-end">
                            <Button color="primary" label="Confirmar" variant="contained" onClick={onBuy} />
                        </Grid>
                    </>
                ) : (
                    <>
                        {isLogo && (
                            <Grid item xs={9}>
                                <div className={classes.footerLogo}>
                                    <Logo className={classes.logo} alt="logo" type="dark" />
                                </div>
                            </Grid>
                        )}
                        <Grid item xs={isLogo ? 3 : 12} className={classes.priceFooter}>
                            <div className={classes.column}>
                                <div
                                    className={classes.row}
                                    style={{
                                        justifyContent: "space-between",
                                        alignItems: "center"
                                    }}
                                >
                                    <Typography type="body2semibold">R$</Typography>
                                    <Typography type="headline3">{totalPricePerAdult}&nbsp;</Typography>
                                    <Typography type="body2"> / Adulto</Typography>
                                </div>
                                <span
                                    style={{
                                        display: "flex",
                                        alignItems: "baseline"
                                    }}
                                >
                                    <Typography className={classes.priceLabel} type="captionregular">
                                        {type !== "packages" ? `${pax} passageiros` : `${pax} viajantes`}
                                    </Typography>
                                    <Typography
                                        type="captionsemibold"
                                        className={
                                            type !== "packages" ? classes.priceTotalAir : classes.priceTotalPackage
                                        }
                                    >
                                        {totalPrice}
                                    </Typography>
                                </span>
                            </div>
                            <Button
                                color="primary"
                                label={labelButton || "Confirmar voo"}
                                variant="contained"
                                onClick={onBuy}
                            />
                        </Grid>
                    </>
                )}
            </Grid>
        );
    };

    return (
        <Box className={classes.root} onClick={onClick}>
            {!isLoading && renderCardPrice()}
            {isLoading && renderLoadingCard()}
        </Box>
    );
};

CardResumedFooter.propTypes = {
    classes: PropTypes.object.isRequired,
    pax: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    price: PropTypes.string.isRequired,
    taxesIncluded: PropTypes.bool,
    parcel: PropTypes.string,
    onClick: PropTypes.func,
    isLoading: PropTypes.bool
};

CardResumedFooter.defaultProps = {
    isLogo: true
};

export default withStyles(styles, { withTheme: true })(CardResumedFooter);
